<template>
  <div>
    <el-select 
      v-model="selectedRace" 
      clearable 
      @clear="clearRace()"
    >
      <el-option
        v-for="item in races"
        :key="item"
        :label="item"
        :value="item"
      />
    </el-select>

    <el-select 
      v-model="selectedCat" 
      clearable
      @clear="clearCat()"
    >
      <el-option
        v-for="item in cats"
        :key="item"
        :value="item"
      />
    </el-select>

    <el-select 
      v-model="selectedTeam" 
      clearable
      @clear="clearTeam()"
    >
      <el-option
        v-for="item in teams"
        :key="item"
        :value="item"
      />
    </el-select>

    <el-row 
      type="flex" 
      justify="center" 
      style="margin-top: 20px">
      <el-col 
        :xs="24" 
        :sm="16" 
        :md="14" 
        :lg="12" 
        :xl="10">
        <span># of places in calculation ({{ numPlaces }})</span>
        <el-slider
          v-model="numPlaces"
          :step="1"
          :min="1"
          :max="20"/>
      </el-col>
    </el-row>

    <el-row 
      type="flex" 
      justify="center" 
      class="row-bg">
      <el-col 
        :xs="4"
        :sm="3" 
        :md="2" 
        :lg="1" 
        :xl="1" 
        align="center"><div>Bib</div></el-col>
      <el-col 
        :xs="4"
        :sm="3" 
        :md="2" 
        :lg="1" 
        :xl="1" 
        align="center"><div>Rank</div></el-col>
      <el-col 
        :xs="4"
        :sm="3" 
        :md="2" 
        :lg="1" 
        :xl="1" 
        align="center"><div>Races</div></el-col>
      <el-col 
        v-if="!hidePlacings"
        :span="4" 
        align="right" 
        class="hidden-sm-and-down cell-margin"><div>Top {{ numPlaces }} finishes</div></el-col>      
      <el-col 
        v-if="!hidePlacings" 
        :span="6" 
        class="hidden-sm-and-down cell-margin"/>      
    </el-row>

    <el-row 
      v-for="rank in rankings" 
      :key="rank.number" 
      type="flex" 
      justify="center" 
      class="row-bg">
      <el-col 
        :xs="4" 
        :sm="3" 
        :md="2" 
        :lg="1" 
        :xl="1"><div style="font-weight: bold">#{{ rank.number }}</div></el-col>
      <el-col 
        :xs="4" 
        :sm="3" 
        :md="2" 
        :lg="1" 
        :xl="1"><div>{{ rank.rank }}</div></el-col>
      <el-col 
        :xs="4" 
        :sm="3" 
        :md="2" 
        :lg="1" 
        :xl="1"><div>{{ rank.races }}</div></el-col>
      <el-col 
        v-if="!hidePlacings" 
        :span="4" 
        class="hidden-sm-and-down cell-margin" 
        align="right"><div 
          v-for="(k,i) in Object.keys(rank.place).sort()" 
          :key="i">{{ k }}</div></el-col>
      <el-col 
        v-if="!hidePlacings" 
        :span="6" 
        class="hidden-sm-and-down cell-margin" 
        align="left"><div 
          v-for="(k,i) in Object.keys(rank.place).sort()" 
          :key="i">{{ rank.place[k].filter(v => v != 100).join(', ') }}</div></el-col>
    </el-row>

    <el-checkbox 
      v-model="hidePlacings" 
      class="hidden-sm-and-down">Hide Placings</el-checkbox>

  </div>
</template>

<script>
import { mapState } from "vuex";
import "element-ui/lib/theme-chalk/display.css";

export default {
  name: "Numbers",

  data() {
    return {
      numPlaces: 10,
      hidePlacings: false
    };
  },

  computed: {
    ...mapState(["selectedRace", "selectedCat"]),

    selectedRace: {
      set(r) {
        this.$store.commit("SelectRace", r);
      },
      get() {
        return this.$store.state.selectedRace;
      }
    },

    selectedCat: {
      set(r) {
        this.$store.commit("SelectCat", r);
      },
      get() {
        return this.$store.state.selectedCat;
      }
    },

    selectedTeam: {
      set(r) {
        this.$store.commit("SelectTeam", r);
      },
      get() {
        return this.$store.state.selectedTeam;
      }
    },

    races() {
      // return Object.keys(this.$store.state.results);
      return [
        "All Races",
        "Alpenrose Dairy Day 1",
        "Alpenrose Dairy Day 2",
        "PIR Heron Lakes"
      ];
    },

    cats() {
      let c = {};
      Object.keys(this.$store.state.results).map(race => {
        this.$store.state.results[race].map(r => {
          c[r.cat] = {};
        });
      });
      return ["All Categories"].concat(Object.keys(c).sort());
    },

    teams() {
      let t = {};
      Object.keys(this.$store.state.results).map(race => {
        this.$store.state.results[race].map(r => {
          t[r.team] = {};
        });
      });
      return ["All Teams"].concat(Object.keys(t).sort());
    },

    rankings() {
      let rankings = [
        { number: 0, races: 0, place: {}, rank: 0 },
        { number: 1, races: 0, place: {}, rank: 0 },
        { number: 2, races: 0, place: {}, rank: 0 },
        { number: 3, races: 0, place: {}, rank: 0 },
        { number: 4, races: 0, place: {}, rank: 0 },
        { number: 5, races: 0, place: {}, rank: 0 },
        { number: 6, races: 0, place: {}, rank: 0 },
        { number: 7, races: 0, place: {}, rank: 0 },
        { number: 8, races: 0, place: {}, rank: 0 },
        { number: 9, races: 0, place: {}, rank: 0 }
      ];

      // pre-fill the places with '100's
      // BUGBUG -- pad after filling to handle races > 100
      this.races.map(race => {
        if (race == "All Races") return;
        rankings.map(r => {
          r.place[race] = new Array(this.numPlaces).fill(100);
        });
      });

      Object.keys(this.$store.state.results).map(race => {
        if (this.selectedRace != "All Races" && race != this.selectedRace) {
          return;
        }

        this.$store.state.results[race].map(r => {
          if (
            this.selectedCat != "All Categories" &&
            r.cat != this.selectedCat
          ) {
            return;
          }
          if (this.selectedTeam != "All Teams" && r.team != this.selectedTeam) {
            return;
          }

          let idx = +r.number % 10;
          if (isNaN(idx)) {
            return;
          }

          rankings[idx].races++;

          if (!isNaN(+r.place) && +r.place > 0) {
            rankings[idx].place[race].push(+r.place);
          }
        });
      });

      rankings.map(v => {
        Object.keys(v.place).map(k => {
          v.place[k] = v.place[k]
            .sort((a, b) => {
              return a > b ? 1 : a < b ? -1 : 0;
            })
            .splice(0, this.numPlaces);

          v.rank += v.place[k].reduce((t, n) => {
            return t + n;
          });
        });
      });

      rankings.sort((a, b) => {
        return a.rank > b.rank ? 1 : a.rank < b.rank ? -1 : 0;
      });

      return rankings;
    }
  },

  created() {
    this.$store.dispatch("loadResults");
  },

  methods: {
    setRace() {},
    setCat() {},
    clearRace() {
      this.selectedRace = "All Races";
    },
    clearCat() {
      this.selectedCat = "All Categories";
    },
    clearTeam() {
      this.selectedTeam = "All Teams";
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}

.cell-margin {
  margin-left: 10px;
  margin-right: 10px;
}
</style>
