import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    selectedRace: "All Races",
    selectedCat: "All Categories",
    selectedTeam: "All Teams",
    results: {}
    // [{
    //   "number":"581",
    //   "place":"1",
    //   "name":"Heather VanValkenburg",
    //   "race_name": "Athena",
    //   "event_full_name": "River City Bicycles Cyclocross Crusade: Alpenrose Dairy Day 1",
    //   "team_name": "Cyclepath Racing",
    // }],
  },
  mutations: {
    SelectRace(state, payload) {
      state.selectedRace = payload;
    },

    SelectCat(state, payload) {
      state.selectedCat = payload;
    },

    SelectTeam(state, payload) {
      state.selectedTeam = payload;
    },

    // clears all the race results
    ClearResults(state) {
      state.results = [];
    },
    // push a race result onto the list
    AddResult(state, payload) {
      // state.results.push(payload);
      state.results = { ...state.results, ...payload };
    }
  },
  actions: {
    loadResults(context) {
      context.commit("ClearResults");

      [
        "/static/alpenrose-day-1",
        "/static/alpenrose-day-2",
        "/static/pir-heron-lakes"
      ].map(url => {
        axios
          .get(url)
          .then(response => {
            context.commit("AddResult", response.data);
          })
          .catch(error => {
            console.log(error);
          });
      });
    }
  }
});
